<template>
  <nav class="navbar navbar-expand-md sticky-top bg-white border-vh-b" v-if="user">
    <div>
      <button
        class="nav-item pl-0 pr-1 navbar-toggler sidebar-toggler"
        role="presentation"
        id="sidebarCollapse"
        data-toggle="collapse"
        data-target="#usersidebar"
        aria-expanded="false"
        aria-controls="navbar"
      >
        <a class="nav-link active" href="#">
          <i class="fa fa-align-left"></i>
        </a>
      </button>

      <router-link to="/" class="navbar-brand brand pl-2" tag="a">
        <img
          src="../assets/appLogo.svg"
          alt="FasDo"
          style="width:2.5em"
          class="align-self-center pr-2"
        />
        <span>फasDo</span>
        <small style="font-size: 0.475em">Fasdo kiya kya?</small>
      </router-link>
    </div>

    <!-- middle -->
    <div class="ml-auto mr-auto pl-4 d-none d-lg-block dropdown" v-if="isLoggedIn && showSearchBar">
      <div class="input-group">
        <div class="input-group-prepend">
          <span class="input-group-text nav-form-bg" id="basic-addon1">
            <i class="fa fa-search"></i>
          </span>
        </div>
        <input
          type="text"
          class="form-control form-control-sm hz-input nav-form-bg p-4 w-custom"
          placeholder="Search Here..."
          aria-label="Search Here..."
          aria-describedby="basic-addon1"
          v-model="input"
          @focus="goToSearch()"
          @keyup.enter="searching()"
        />
      </div>
    </div>

    <div style="right:15px">
      <ul class="navbar-nav">
        <li class="nav-item align-self-center pr-4 pb-1 dropdown">
          <div
            class="unread-notification"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            <img src="../assets/bell.svg" style="width:17px" />
            <span class="badge badge-danger">{{countUnreadNotification}}</span>
          </div>
          <div class="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdownMenuLink">
            <div class="dropdown-item">
              <span @click.prevent="markAsRead()">Mark all as read</span>
            </div>
            <div class="dropdown-divider"></div>
            <span v-for="notification in notifications" :key="notification.id">
              <router-link
                :to="{name: 'ViewOrder', params: {orderId: notification.data.orderId}}"
                tag="div"
                class="dropdown-item"
                v-if="notification.id!=null"
              >{{notification.data.message}}</router-link>

              <router-link
                :to="{name: 'ViewOrder', params: {orderId: notification.data.orderId}}"
                tag="div"
                class="dropdown-item"
                style="background:#d4d1d1;"
                v-else
              >{{notification.data.message}}</router-link>
            </span>
            <!-- /dropdown-toolbar -->

            <div class="dropdown-footer text-center">
              <!-- <a href="#">View All</a> -->
            </div>
            <!-- /dropdown-footer -->
          </div>
          <!-- /dropdown-container -->
        </li>
        <li class="nav-item align-self-center pl-2 pr-4 pb-1">
          <button
            class="d-block navbar-toggler sidebar-toggler"
            role="presentation"
            id="sidebarCollapse"
            data-toggle="collapse"
            data-target="#usersettingSidebar"
            aria-expanded="true"
            aria-controls="navbar"
            @click="switchClass"
          >
            <img src="../assets/gear.svg" style="width:17px" />
          </button>
        </li>
        <li class="nav-item small">
          <router-link to="/login" class="nav-link" v-if="!isLoggedIn">
            <i class="fas fa-power-off pr-1"></i>
            <span class="d-none d-lg-block">Login</span>
          </router-link>
        </li>

        <li class="nav-item small" v-if="user">
          <span class="nav-link d-none d-lg-block" v-if="isLoggedIn">Hi,{{user.name}}</span>
        </li>

        <li class="nav-item small">
          <a
            href="#"
            class="nav-link d-flex align-items-center"
            @click.prevent="logout"
            v-if="isLoggedIn"
          >
            <i class="fas fa-power-off pr-1"></i>
            <span class="d-none d-lg-block">Logout</span>
          </a>
        </li>
      </ul>
    </div>
  </nav>
</template>


<script>
import axios from "axios";
import db from "./firebaseInit";
import firebase from "firebase";
import store from "@/store/store";
import { domain } from "@/store/variable";
import { bus } from "../main";

export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false,
      search: [],
      input: null,
      showSearchBar: true,
      notifications: [],
      countUnreadNotification: 0

      // stockLess: 0,
      // outOfStock: 0
    };
  },

  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
    }
  },

  mounted() {
    setTimeout(() => {
      this.getNotifications();
    }, 2000);
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  watch: {
    $route: function() {
      // Check if given route is true, if it is then hide Nav.
      if (this.$route.path === "/order/place") {
        this.showSearchBar = false;
      } else {
        this.showSearchBar = true;
      }
    }
  },

  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    },

    goToSearch() {
      if (this.$route.name != "searchItem") {
        if (confirm("You'll be taken to search result page.")) {
          this.$router.push({ name: "searchItem" });
        } else {
          // do nothing
        }
      }
    },

    getNotifications() {
      if (this.user) {
        this.notifications = [];
        let url = domain + "user/notification";
        axios
          .get(url, {
            headers: {
              "X-api-token": firebase.auth().currentUser.uid
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              this.notifications.unshift(doc);
              if (doc.readAt == null) {
                this.countUnreadNotification++;
              }
            });
          });
      }
    },

    markAsRead() {
      let url = domain + "user/notification/" + this.user.uuid;
      axios
        .post(
          url,
          {
            _method: "put",
            markAsRead: 1
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          if (res.data) {
            console.log(res.data);
            Vue.set(this.items[index], "active", res.data.active);
          }
        });
    },

    searching() {
      if (this.input) {
        var searched = this.input;
        bus.$emit("searchItem", { searched });
      }
    },

    switchClass() {
      bus.$emit("switchClass");
    }
  }
};
</script>

<style scoped>
.brand {
  font-weight: 800;
  color: #004d40;
}

.nav-form-bg {
  background-color: #fbfbfb;
}

.w-custom {
  width: 32rem;
}

.unread-notification {
  position: relative;
}

.unread-notification span {
  position: absolute;
  top: -6px;
  left: 6px;
}

a {
  text-decoration: none;
  color: #263238;
}

a:hover {
  color: #263238;
}
</style>



